* {
  padding: 0px;
  margin: 0px;
}
body {
  font-family: 'Inter', sans-serif;
  background-color: #ffffff;
}
.container {
  margin-left: auto;
  margin-right: auto;
}
@media only screen and (min-width: 768px) {
  .container {
    max-width: 760px;
  }
}
@media only screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}
